const SET_USER_DATA = 'auth/SET_USER_DATA';
const SET_IS_AUTH = 'auth/SET_IS_AUTH';
const SET_IS_FETCHING = 'auth/SET_IS_FETCHING';
// const SET_CAPTCHA = 'auth/SET_CAPTCHA';

let initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
  isFetching: false,
  captcha: null,
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA: {
      return {
        ...state,
        ...action.payload,
      };
    }
    case SET_IS_AUTH: {
      return {
        ...state,
        ...action.payload,
      };
    }
    case SET_IS_FETCHING: {
      return {
        ...state,
        ...action.payload,
      };
    }
    default:
      return state;
  }
};

/**
 * Actions
 */

// —Ö–∑ –∫–∞–∫ –ª—É—á—à–µ —Ç–∏–ø—ã –æ–ø–∏—Å—ã–≤–∞—Ç—å, –Ω–æ –≤ —Ä–µ–¥—å—é—Å–µ—Ä–∞—Ö —Å–æ–≤—Å–µ–º –±–µ–∑ –Ω–∏—Ö –±—É–¥–µ—Ç –±–æ–ª—å, –º–± –∏—Ö –Ω–∞ —Ç—Å –ø–∏—Å–∞—Ç—å?
// –ú–æ–∂–Ω–æ —Ö–æ—Ç—è –±—ã –≤ jsdoc –ø–∏—Å–∞—Ç—å :)

/**
 * @param {Number} userId
 * @param {String} email
 * @param {String} login
 * @param {Boolean} isAuth
 */

export const setAuthUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA,
  payload: { userId, email, login, isAuth },
});

/**
 * type setIsAuthType = {
 * type: typeof SET_IS_AUTH,
 * isAuth: boolean
 * }
 */

export const setIsAuth = isAuth => ({
  type: SET_IS_AUTH,
  payload: { isAuth },
});

/**
 * type setIsFetchingType = {
 * type: typeof SET_IS_FETCHING,
 * isFetching: boolean
 * }
 */

export const setIsFetching = isFetching => ({
  type: SET_IS_FETCHING,
  payload: { isFetching },
});

/**
 * Thunks. –í—Å—è –∞—Å–∏–Ω—Ö—Ä–æ–Ω—â–∏–Ω–∞ —Ç—É—Ç.
 */

export const getAuthUserDataThunk = (time = 8000) => async dispatch => {
  const sleep = ms => new Promise(r => setTimeout(r, ms));

  dispatch(setIsFetching(true));
  await sleep(time);
  console.log('getAuthUserDataThunk');
  dispatch(setIsFetching(false));

  dispatch(setIsAuth(true));
};

export default authReducer;
