const SET_USER_DATA = 'game/SET_USER_DATA';
const SET_IS_FETCHING = 'game/SET_IS_FETCHING';
const SET_GAMES_LIST = 'game/SET_GAMES_LIST';

// const SET_CAPTCHA = 'auth/SET_CAPTCHA';

