const getInitialGame = () => {
  const id = Math.random();
  const size = (5 + id * 20) ^ 0;

  return {
    id,
    name: 'Game name ' + ((id * 1000) ^ 0),
    date: new Date(),
    size,
    players: [
      {
        name: 'Lupusregina[beta]',
        id: Math.random(),
      },
      {
        name: 'Den Ri',
        id: Math.random(),
      },
      {
        name: '–ó–æ–º–±–∏ Ich bin Roboter',
        id: Math.random(),
      },
      {
        name: 'Bunk Bunkovich',
        id: Math.random(),
      },
      {
        name: 'Aleksander irreligious86',
        id: Math.random(),
      },
      {
        name: 'Anton Miroshnichenko',
        id: Math.random(),
      },
      {
        name: 'Ilya Kozyura',
        id: Math.random(),
      },
      {
        name: 'Chingiz Mamyev',
        id: Math.random(),
      },
      {
        name: 'pacificescape',
        id: Math.random(),
      },
    ].slice(0, size),
    status: 9 < size ? 1 : 0, // 1 - open, 0 -- close ???
  };
};

let initialState = {
  lobby: [],
  currentGame: {},
  theme: {},
};

const gameReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_GAMES_LIST: {
      return {
        ...state,
        ...action.payload,
      };
    }
    case SET_IS_FETCHING: {
      return {
        ...state,
        ...action.payload,
      };
    }
    default:
      return state;
  }
};

/**
 * Actions
 */

// —Ö–∑ –∫–∞–∫ –ª—É—á—à–µ —Ç–∏–ø—ã –æ–ø–∏—Å—ã–≤–∞—Ç—å, –Ω–æ –≤ —Ä–µ–¥—å—é—Å–µ—Ä–∞—Ö —Å–æ–≤—Å–µ–º –±–µ–∑ –Ω–∏—Ö –±—É–¥–µ—Ç –±–æ–ª—å, –º–± –∏—Ö –Ω–∞ —Ç—Å –ø–∏—Å–∞—Ç—å?

/**
 * @param {Number} userId
 * @param {String} email
 * @param {String} login
 * @param {Boolean} isAuth
 */

export const setAuthUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA,
  payload: { userId, email, login, isAuth },
});

/**
 * type setIsAuthType = {
 * type: typeof SET_GAMES_LIST,
 * isAuth: boolean
 * }
 */

export const setGamesList = lobby => ({
  type: SET_GAMES_LIST,
  payload: { lobby },
});

/**
 * type setIsFetchingType = {
 * type: typeof SET_IS_FETCHING,
 * isFetching: boolean
 * }
 */

export const setIsFetching = isFetching => ({
  type: SET_IS_FETCHING,
  payload: { isFetching },
});

/**
 * Thunks. –í—Å—è –∞—Å–∏–Ω—Ö—Ä–æ–Ω—â–∏–Ω–∞ —Ç—É—Ç.
 */

const sleep = ms => new Promise(r => setTimeout(r, ms));

export const getGameListThunk = (page = 0) => async dispatch => {
  dispatch(setIsFetching(true));
  await sleep(1 * 1e3);

  const games = Array(10).fill(null).map(getInitialGame);

  dispatch(setGamesList(games));
  dispatch(setIsFetching(false));
};

export default gameReducer;
